import React from 'react'
import FileViewer from 'react-file-viewer';

export default function TestPdf({src}) {
  return (
    <div>
        <p>
            TestPdf
        </p>
        <FileViewer
            controls={true}
            fileType={"pdf"}
            filePath={require(src)}
            // key={src}
        />
    </div>
  )
}
